.map {
    background: linear-gradient(180deg, rgb(200, 236, 251), rgb(166, 227, 253));
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        @media (max-width: 1200px) {
            width: 80%;
        }

        @media (max-width: 768px) {
            width: 100%;
        }

        h2 {
            font-family: "Suez One", serif;
            font-size: 3.2em;
            color: rgb(247, 224, 75);
            text-shadow: 2px 4px 10px rgb(206, 171, 15);
            margin-bottom: 17%;

            @media (max-width: 1200px) {
                margin-bottom: 10%;
            }
        }
    }

    &__yandex {
        border-radius: 30px;
        box-shadow: 0px 0px 30px rgb(130, 159, 255);

        @media (max-width: 1200px) {
            width: 70%;
        }

        @media (max-width: 768px) {
            width: 80%;
        }

        iframe {
            @media (max-width: 1200px) {
                width: 100%;
            }
        }
    }
}