.entertainments {
    height: 100vh;
    background: linear-gradient(180deg,
            rgb(32, 146, 64),
            rgb(250, 250, 176) 30%,
            rgb(255, 253, 209) 100%);
    display: flex;
    justify-content: center;
    align-items: center;

    &__container {
        height: 65%;
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 1200px) {
            width: 100%;
        }

        @media (max-width: 768px) {
            height: 80%;
        }

        @media (max-width: 480px) {
            height: 100%;
            justify-content: center;
        }
    }

    &__title {
        font-family: "Suez One", serif;
        font-size: 3.5em;
        color: rgb(247, 224, 75);
        text-shadow: 2px 4px 10px rgb(206, 171, 15);

        @media (max-width: 480px) {
            font-size: 2.4em;
            margin-bottom: 5%;
        }

        @media (max-width: 320px) {
            font-size: 2em;
        }
    }

    &__block {
        height: 70%;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: 0;

        @media (max-width: 1200px) {
            width: 80%;
        }

        @media (max-width: 768px) {
            height: 88%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
        }

        @media (max-width: 480px) {
            height: 80%;
        }
    }

    &__cards {
        height: 90%;
        width: 27%;
        padding: 20px;

        background-color: rgb(255, 229, 35);
        box-shadow: 0px 4px 10px rgb(206, 171, 15);
        border-radius: 13px;

        font-family: "Indie Flower", cursive;
        font-size: 1.4em;
        color: rgb(53, 66, 138);
        font-weight: 400;
        text-align: center;

        transition: all 0.5s ease-in-out 0.08s;

        @media (max-width: 1200px) {
            width: 25%;
        }

        @media (max-width: 1000px) {
            width: 30%;
        }

        @media (max-width: 768px) {
            height: 42%;
            transition: none;
            width: 35%;
            font-size: 1.1em
        }

        @media (max-width: 480px) {
            height: 31%;
            width: 70%;
            font-size: 1.1em;
        }

        @media (max-width: 320px) {
            font-size: 0.95em;
        }

        &:first-child {
            @media (max-width: 768px) {
                margin-right: 5%;
            }

            @media (max-width: 480px) {
                margin-right: 0;
            }
        }

        &:hover {
            transform: scale(1.2) translateY(20px);
            box-shadow: 0px 4px 20px rgb(206, 171, 15);

            @media (max-width: 992px) {
                transform: none;
                box-shadow: 0px 4px 10px rgb(206, 171, 15);
            }

            @media (max-width: 768px) {
                transform: none;
                box-shadow: 0px 4px 10px rgb(206, 171, 15);
            }
        }
    }

    &__cards-img {
        height: 65%;
        width: 100%;
        margin-bottom: 8%;
        border-radius: 10px;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        @media (max-width: 1200px) {
            height: 55%;
        }

        @media (max-width: 480px) {
            height: 50%;
        }
    }

    .card__img1 {
        background-image: url(../image/lager.webp);
    }

    .card__img2 {
        background-image: url(../image/mount.webp);
    }

    .card__img3 {
        background-image: url(../image/attraction.webp);
    }
}