%position_anim_cloud {
    position: absolute;
    height: 100%;
    width: auto;

    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.header {
    height: 100vh;
    background-image: url(../image/background.webp);
    background-size: cover;
    background-position: bottom left;
    background-repeat: repeat;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    overflow: hidden;

    &__sky {
        position: relative;
        height: 14%;
    }

    &__sky-first {
        @extend %position_anim_cloud;
        animation-name: first_sky;
        animation-duration: 3s;

        @media (max-width: 768px) {
            height: 50%;
        }

        @media (max-width: 320px) {
            display: none;
        }
    }

    &__sky-second {
        @extend %position_anim_cloud;
        animation-name: second_sky;
        animation-duration: 5s;

        @media (max-width: 768px) {
            height: 50%;
        }
    }

    &__middle {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        height: 20%;

        span {
            width: 14%;

            @media (max-width: 1200px) {
                width: 20%;
            }

            @media (max-width: 992px) {
                width: 26%;
            }

            @media (max-width: 768px) {
                display: none;
            }
        }
        @media (max-width: 768px) {
            justify-content: center;
        }
    }

    &__middle-logo {
        font-family: "Suez One", serif;
        font-size: 3.5em;
        color: rgb(247, 224, 75);
        text-shadow: 2px 4px 10px rgb(206, 171, 15);
        animation: logo 7s infinite;

        @media (max-width: 992px) {
            font-size: 2.7em;
        }

        @media (max-width: 768px) {
            font-size: 3.7em;
        }

        @media (max-width: 320px) {
            font-size: 3em;
        }
    }

    &__middle-menu {
        margin-top: 1%;
        width: 17%;

        list-style: none;
        color: rgb(247, 224, 75);
        font-size: 1.6em;
        font-family: "Indie Flower", cursive;
        font-weight: 700;

        @media (max-width: 1200px) {
            margin-right: 3%;
        }

        @media (max-width: 992px) {
            margin-right: 8%;
            font-size: 1.5em;
        }

        @media (max-width: 768px) {
            display: none;
        }

        li {
            margin-bottom: 12px;
            margin-left: 10px;
            transition: all 0.3s ease;
        }

        li:first-child {
            animation: menu1 6s infinite ease;
        }

        li:nth-child(2) {
            animation: menu1 6s infinite ease 0.2s;
        }

        li:nth-child(3) {
            animation: menu1 6s infinite ease 0.4s;
        }

        li:last-child {
            animation: menu1 6s infinite ease 0.6s;
        }

        li:hover {
            color: rgb(255, 251, 17);
            text-shadow: 4px 4px 6px rgb(206, 171, 15);
            margin-left: 7px;
            transform: scale(1.1);
        }
    }

    &__bottom {
        height: 37%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @media (max-width: 1200px) {
            height: 32%;
        }
    }

    &__bottom-tree {
        position: relative;
        height: 38%;

        img {
            position: absolute;
            opacity: 0;

            @media (max-width: 992px) {
                display: none;
            }
        }

        img:first-child {
            left: 15%;
            top: 1%;
            animation: list1 7s infinite ease;
        }

        img:nth-child(2) {
            left: 16%;
            top: 8%;
            animation: list2 7s infinite 2s ease;
        }

        img:nth-child(3) {
            left: 17%;
            top: 40%;
            animation: list3 7s infinite 3.5s ease;
        }

        img:nth-child(4) {
            left: 15%;
            top: 60%;
            animation: list4 7s infinite 5s ease;
        }
    }



    &__bottom-road {
        position: relative;
        height: 29%;

        img:first-child {
            position: absolute;
            top: -7%;
            height: 60%;
            animation: car1 11s infinite linear;
            opacity: 0;
        }

        img:nth-child(2) {
            position: absolute;
            top: 38%;
            height: 60%;
            animation: car2 12s infinite 2s linear;
            opacity: 0;
        }

        img:nth-child(3) {
            position: absolute;
            top: 38%;
            height: 60%;
            animation: car3 12s infinite 6s linear;
            opacity: 0;

            @media (max-width: 480px) {
                display: none;
            }
        }
    }
}


// -------------------- KEYFRAMES SKY -------------------

@keyframes first_sky {
    0% {
        left: -10%;
    }

    100% {
        left: 0%;
    }
}

@keyframes second_sky {
    0% {
        right: -10%;
    }

    100% {
        right: 5%;
    }
}

// -------------------- // KEYFRAMES SKY -------------------

// ------------------- KEYFRAMES LOGO -----------------

@keyframes logo {
    0% {
        transform: scale(1);
    }

    3% {
        transform: scale(1.12);
    }

    6% {
        transform: scale(1);
    }

    10% {
        transform: rotate(5deg);
    }

    11% {
        transform: rotate(-5deg);
    }

    12% {
        transform: rotate(5deg);
    }

    13% {
        transform: rotate(-5deg);
    }

    14% {
        transform: rotate(0deg);
    }
}

// ------------------- // KEYFRAMES LOGO -----------------


// --------------- KEYFRAMES MENU ----------------------

@keyframes menu1 {
    0% {
        margin-left: 10px;
    }

    90% {
        margin-left: 10px;
    }

    95% {
        margin-left: 0px;
    }

    100% {
        margin-left: 10px;
    }
}

// ----------------- // KEYFRAMES MENU ----------------------


// -----------------  KEYFRAMES TREE  ---------------

@keyframes list1 {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    80% {
        opacity: 1;
    }

    100% {
        left: 28%;
        top: 8%;
        opacity: 0;
        transform: rotate(120deg);
    }
}

@keyframes list2 {
    0% {
        opacity: 0;
    }

    30% {
        opacity: 1;
    }

    70% {
        opacity: 1;
    }

    100% {
        left: 35%;
        top: 12%;
        opacity: 0;
        transform: rotate(180deg);
    }
}

@keyframes list3 {
    0% {
        opacity: 0;
    }

    35% {
        opacity: 1;
    }

    75% {
        opacity: 1;
    }

    100% {
        left: 28%;
        top: 48%;
        opacity: 0;
        transform: rotate(180deg);
    }
}

@keyframes list4 {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    60% {
        opacity: 1;
    }

    100% {
        left: 40%;
        top: 70%;
        opacity: 0;
        transform: rotate(150deg);
    }
}

// ----------------- // KEYFRAMES TREE  ---------------

// -------------------  KEYFRAMES CARS ------------------

@keyframes car1 {
    0% {
        right: -10%;
        opacity: 1;
    }

    100% {
        right: 110%;
        opacity: 1;
    }
}

@keyframes car2 {
    0% {
        left: -10%;
        opacity: 1;
    }

    100% {
        left: 110%;
        opacity: 1;
    }
}

@keyframes car3 {
    0% {
        left: -10%;
        opacity: 1;
    }

    100% {
        left: 110%;
        opacity: 1;
    }
}

// ------------------- // KEYFRAMES CARS ------------------