.callback {
    background: linear-gradient(180deg, rgb(255, 253, 186), rgb(200, 236, 251));
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    &__wrapper {
        height: 70%;
        width: 60%;
        min-width: 500px;

        display: flex;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 992px) {
            width: 75%;
        }

        @media (max-width: 768px) {
            min-width: 100%;
            width: 100%;
            justify-content: center;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 50%;
        min-width: 250px;

        @media (max-width: 992px) {
            width: 50%;
        }

        @media (max-width: 768px) {
            width: 80%;
        }
    }

    &__title {
        font-family: "Suez One", serif;
        font-size: 3.2em;
        color: rgb(247, 224, 75);
        text-shadow: 2px 4px 10px rgb(206, 171, 15);
        margin-bottom: 15%;
    }

    &__form {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        height: 40%;
        width: 100%;

        font-family: "Indie Flower", cursive;

        input[type="button"] {
            height: 20%;
            width: 60%;
            background-color: rgb(247, 224, 75);
            border: none;
            border-radius: 100px;
            font-size: 1.3em;
            box-shadow: 1px 4px 6px rgb(222, 224, 98);
        }

        input[type="button"]:hover {
            background-color: rgb(255, 227, 46);
            font-weight: 700;
        }
    }

    &__form-group {
        height: 25%;
        width: 100%;
        margin-bottom: 10%;
        position: relative;

        input {
            background-color: rgb(255, 251, 211);
            height: 100%;
            width: calc(100% - 15px);
            border: none;
            border-radius: 100px;
            outline: none;
            padding: 0;
            padding-left: 15px;
            font-family: "Indie Flower", cursive;
            font-size: 1.5em;
            box-shadow: 2px 2px 6px rgba(228, 194, 43, 0.793);
        }

        input::placeholder {
            margin-left: 10px;
        }

        input:focus~label {
            top: -10%;
            left: 7%;
            font-size: 1em;
        }

        input:not(:placeholder-shown)~label {
            top: -10%;
            left: 7%;
            font-size: 1em;
        }

        label {
            position: absolute;
            top: 50%;
            left: 5%;
            transform: translateY(-50%);
            font-size: 1.5em;
            transition: .5s all ease-in-out;
        }
    }

    &__img {
        height: 70%;
        width: 40%;
        background-image: url(../image/contacts.webp);
        background-size: cover;
        background-position: center;
        border-radius: 30px;
        box-shadow: 0px 0px 30px rgb(239, 219, 128);

        @media (max-width: 1200px) {
            height: 60%;
        }

        @media (max-width: 768px) {
            display: none;
        }
    }
}