.rules {
    height: 100vh;
    background: linear-gradient(180deg,
            rgb(255, 253, 209),
            rgb(255, 253, 186) 28%,
            rgb(255, 253, 186) 100%);
    display: flex;
    justify-content: center;
    align-items: center;

    &__container {
        display: flex;
        justify-content: center;
        width: 100%;
        height: 50%;

        @media (max-width: 768px) {
            width: 70%;
            height: 80%;
        }

        @media (max-width: 320px) {
            height: 90%;
        }
    }

    &__img {
        background-image: url(../image/rules_bg.webp);
        background-size: cover;
        background-position: center;

        height: 100%;
        width: 30%;
        border-radius: 30px;
        box-shadow: 0px 0px 30px rgb(206, 171, 15);
        margin-right: 7%;

        @media (max-width: 1200px) {
            margin-right: 4%;
        }

        @media (max-width: 992px) {
            width: 37%;
        }

        @media (max-width: 768px) {
            display: none;
        }
    }

    &__list {
        max-width: 400px;
        padding: 10px 30px;
        border: 4px dashed rgb(247, 224, 75);
        border-radius: 30px;

        @media (max-width: 992px) {
            max-width: 250px;
        }

        @media (max-width: 768px) {
            height: 80%;
            width: 100%;
            max-width: 100%;
        }

        @media (max-width: 480px) {
            height: 100%;
        }
    }

    &__list-title {
        font-family: "Suez One", serif;
        font-size: 3.5em;
        color: rgb(247, 224, 75);
        text-shadow: 2px 4px 10px rgb(206, 171, 15);
        margin-bottom: 12%;

        @media (max-width: 992px) {
            font-size: 3em;
        }

        @media (max-width: 768px) {
            text-align: center;
        }
    }

    &__list-list {
        list-style: none;
        padding: 0;
        margin: 0;

        @media (max-width: 1200px) {
            max-width: 350px;
        }

        @media (max-width: 768px) {
            max-width: 100%;
        }

        li {
            margin-bottom: 8%;
            font-family: "Indie Flower", cursive;
            font-size: 1.5em;
            color: rgb(53, 66, 138);
            font-weight: 400;
            animation: rules 10s infinite ease;

            @media (max-width: 1200px) and (max-height:700px) {
                font-size: 1.1em;
                margin-bottom: 2.5%;
            }

            @media (max-width: 992px) {
                font-size: 1.25em;
            }

            @media (max-width: 768px) {
                font-size: 1.6em;
            }

            @media (max-width: 480px) {
                font-size: 1.3em;
            }

            @media (max-width: 320px) {
                animation: none;
            }
        }

        li:first-child {
            animation-delay: 2s;
        }

        li:nth-child(2) {
            animation-delay: 2.45s;
        }

        li:nth-child(3) {
            animation-delay: 2.9s;
        }

        li:last-child {
            animation-delay: 3.35s;
            margin-bottom: 0;
        }
    }
}

// --------------------- KEYFRAMES RULES -------------------

@keyframes rules {
    0% {
        color: rgb(53, 66, 138);
    }

    80% {
        color: rgb(53, 66, 138);
        transform: scale(1);
    }

    90% {
        color: rgb(15, 22, 64);
        transform: scale(1.1);
    }

    100% {
        color: rgb(53, 66, 138);
        transform: scale(1);
    }
}

